@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300;1,400;1,600;1,700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif
}
html {
	min-height: 100%;
  position: relative;
}
body {
  font-family: 'Source Sans Pro', sans-serif ;
  background-color: #eff3f6 !important;
  font-size: 12px;
}

:focus {
  outline: none;
}
.pt-6 {
  padding-top: 60px;
}
.px-6 {
  padding-left: 60px;
  padding-right: 60px;
}
.pt-30 {
  padding-top: 30px;
}
.px-30 {
  padding-left: 30px;
  padding-right: 30px;
}

/* ------------ Color set --------- */
.text-primary {
  color:#2398f1 !important
}
.text-secondary {
  color: #354052 !important;
}
.text-success {
  color: #68c574 !important;
}
.text-warning {
  color: #f2a80a !important;
}
.text-danger {
	color: #fc565e !important;
}


.badge-primary {
  background-color:#2398f1 !important;
}
.badge-secondary {
  background-color:#354052 !important;
}
.badge-success {
  background-color:#68c574 !important;
}
.badge-danger {
  background-color:#f86b67 !important;
}

button:focus {
  outline: none;
  box-shadow: none;
}

#sidebar {
  height: 0;
}

.sidebar-left {
	background-color: #222c3c !important;
	width: 350px;
	min-height: 100%;
	position: absolute;
	left: 0;
  z-index:9;
  transition: all 0.5s ease-in-out;
  top: 0;  
}
#sidebar:not(.show) {
  display:block;
}

.sidebar-left .sidebar-column-1 {
	background-color: #1d2531;
	padding: 25px 0;
	width: 100px;
	flex-shrink: 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.sidebar-left .user-list .user-image {
  opacity: 0.5;
  padding: 0 25px;
 
  background-repeat: no-repeat;
  background-position: left center;
}
.sidebar-left .user-list .user-image.active {
  opacity: 1;
  background-image: url('./images/active-icon.png');
}
.sidebar-left .bottom-icon-group {
	padding: 0 25px;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}
.sidebar-left .bottom-icon-group a {
  color: #475364;
  margin: 0 5px;
}


.sidebar-column-2 {
  width:calc(100% - 100px);
}

.sidebar-menu {
  flex:auto;
}
.display-6 {
  color: #5b6779;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
}
.sidebar-menu ul li a {
  color: #fff;
  padding:10px 30px;
  font-size: 14px;
  font-weight: 600;
} 
.sidebar-menu ul li a:hover,
.sidebar-menu ul li a.active {
  background-color: #313d4f;
}

.sidebar-left .search-bar .input-grp {
  border-bottom: 1px solid #313d4f;
  margin:20px 0;
}
.search-bar input {
	background: none;
	border: none;
	color: #fff;
	width: 100%;
	flex: auto;
	min-width: 0;
	height: 40px;
	padding-right: 25px;
	box-sizing: border-box;
	max-width: 100%;
}
.search-bar input:focus {
  outline: none;
}
.search-bar button {
	color: #8b95a6;
	margin-left: auto;
	position: absolute;
	bottom: 0;
	right: 0;
	padding: 0;
  height: 40px;
}
.search-bar button:hover {
  opacity: 0.8;
  color: #fff !important;
}

.friends-list-block li {
	display: flex;
	color: #fff;
	margin: 0 0 20px;
	align-items: center;
}
.friends-list-block li .avtar-wrap {
  margin-right: 10px;
  position: relative;
  max-width:40px;
}
.friends-list-block li h4 {
  font-size: 14px;
  margin: 0;
}
.friends-list-block li p {
  margin: 0;
  color:#7c8ca5;
  font-size: 14px;
  font-weight: 600;
}
.friends-list-block li .badge {
  margin-left: auto;
}

 .icon-status {
	width: 20px;
	height: 20px;
	background: #222c3c;
	position: absolute;
	bottom: -4px;
	border-radius: 50%;
	padding: 4px;
	font-size: 13px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #1bb934;
	right: -2px;
}


.navbar-btn {
	position: absolute;
	top: 0;
	right: -30px;
	border: 0 none;
	border: 0 none;
	background-color: #1d2531 !important;
	color: #fff;
	width: 30px;
	height: 30px;
	border-radius: 0 !important;
	text-align: center;
	padding: 0 !important;
	box-shadow: -1px 3px 4px 0px rgba(0,0,0,0.3);
}
.navbar-btn i {
	display: none;
	font-size: 16px;
}
.navbar-btn.collapsed .fa-chevron-right {
  display: block;
}
.navbar-btn:not(.collapsed) .fa-chevron-left {
  display: block;
}




/* ----------------- Header Section ------------- */

.header-section {
	box-shadow: 0px 0px 20px 0px rgba(0,0,0,.1);
  padding:20px 30px;
}
.header-section h2 {
	color: #354052;
	font-size: 24px;
	font-weight: 400;
	margin: 0;
}
.header-section .user-header .img-wrap {
  max-width: 50px;
  margin-right: 10px;
}
.header-section .user-header h4 {
  font-size: 16px;
  color: #2399f1;
  font-weight: 600;
  margin:0;
}
.header-section .user-header p {
  font-size:14px;
  color:#b5b5b5;
  font-weight: 600;
  margin: 0;
}
.header-section .user-header .icon {
	color: #b5b5b5;
	font-size: 12px;
	margin-left: 20px;
}

.main-wrapper {
  margin-left: 350px;
}

/* ------- Card Block ------*/
.cards-box-container .card {
  border-color:#e6eaee;
}
.cards-box-container .card h3 {
  font-size: 30px;
  font-weight: 400;
  color:#354052;
}
.cards-box-container .card h5 {
  color:#4695d9;
  font-size:12px;
  font-weight:600;
  text-transform:uppercase;
  margin:0;
}
.cards-box-container .card .card-footer  {
  background-color: #f5f8fa;
  border-color:#e6eaee;
}
.cards-box-container .card .card-footer p {
  margin:0;
  text-transform: uppercase;
}
.cards-box-container .card .card-footer p i {
	color: #1bb934;
	margin-right: 10px;
	transform: rotate(22deg);
}
.cards-box-container .card.decrease .card-footer p i {
	color: #f05352;
	transform: rotate(-35deg);
}



/* ----------- Profile Card ----------- */

.profile-card .edit-button {
	position: absolute;
	width: 40px;
	height: 40px;
	border-radius: 50% !important;
	border: 1px solid #ebf1f5;
	background-color: #f5f8fa;
	top: -10px;
	right: -10px;
}
.profile-card .cover-img img {
	height: 120px;
	object-fit: cover;
	object-position: center;
	width: 100%;
}
.profile-card .user-img-name {
  margin-top:-80px;
  border-bottom: 1px solid #ebf1f5;
  padding-bottom: 25px;
  margin-bottom:20px;
}
.profile-card .user-img-name .img-wrap {
  max-width: 155px;
  margin:0 auto 18px;
}
.profile-card .user-img-name h3 {
	color: #47505c;
	font-size: 24px;
	font-weight: 600;
	margin: 0;
}
.profile-card .user-img-name p {
  margin:0;
  color:#b5b5b5;
}
.profile-card .user-details .list-group-item {
  border:0 none;
  padding:0;
  margin: 0 0 15px;
}
.profile-card h5 {
	font-size: 12px;
	color: #818ea7;
	font-weight: 400;
	margin: 0 0 5px;
}
.profile-card  h3 {
	font-size: 16px;
	color: #56688a;
	font-weight: 600;
	margin: 0 0 ;
}
.profile-card .progress-wrap {
	align-items: center;
}
.profile-card .progress-wrap .progress {
  flex: auto;
  height:4px;
  max-width: 156px;
}
.profile-card .progress-wrap .progress-bar {
	width: 50%;
}
.profile-card .progress-wrap h5 {
  margin: 0 0 0 15px;
  font-weight: 600;
  color: #353c48;
  font-size: 14px;
}
.profile-card .progress-wrap h5 span {
  color:#2399f1;

}
.profile-card .user-details  p,
.event-card p   {
  font-size: 14px;
  color: #56688a;
}
.profile-card .badge {
	font-size: 10px;
	text-transform: uppercase;
	font-weight: 600;
	padding: 5px 7px;
  margin-right: 8px;
  margin-bottom: 5px;
  background-color: #0474c8;
  color:#fff;
}
.profile-card .badge:nth-child(2)  {
  background-color: #45b854;
}
.profile-card .badge:nth-child(3)  {
  background-color: #8191a6;
}
.profile-card .badge:nth-child(4)  {
  background-color: #f86b67;
}
.event-card ul {
  margin: 0;
}
.event-card ul li {
	display: flex;
	justify-content: space-between;
  margin: 0 0 10px;
}
.event-card ul li .event-text {
  padding-right: 10px;
}
.event-card ul li p {
  margin: 0;
}
.event-card ul li:last-child {
  margin-bottom: 0;
}



/* ------ Table  ---------- Table */

div table {
  background-color: #f5f8fa;
}
div table thead {
  border: 1px solid #e7e9ed !important ;
  border-bottom:0 none !important  ;
}
div table tr th, div table tr td {
	border: 0 none !important;
  vertical-align: middle;
}
div table tbody tr  {
  border: 1px solid #e7e9ed !important ;
  border-top:0 none !important ;
}
div table tbody tr:nth-child(2n + 2) {
  background-color: #f5f8fa;
  border-left-color: #f5f8fa !important ;
  border-right-color: #f5f8fa !important ;
}
div table tbody tr:last-child{
  border-bottom: 0 none !important;
}
div table th, div table td {
  font-size: 14px;
  color: #333c48;
  font-weight:600;
}
div table thead th {
  color: #7f8fa4;
  border-bottom: 0 none !important;
  vertical-align: middle;
  padding-top:15px !important;
}
.table-wrap table tr th span {
  font-size:24px;
  color: #354052;
}
.table-wrap table th,
.table-wrap table td {
	padding: 0.4rem 1.90rem;
}
.table-wrap table tr:first-child td {
  border-top: 0 none;
}

.high-chart-header ul li a {
	color: #b5b5b5;
	border-radius: 40px;
	padding: 4px 12px;
	font-size: 12px;
	text-transform: uppercase;
	font-weight: 600;
}
.high-chart-header ul li a.active {
  border-radius: 40px;
}
 

/* -------- Responsive CSS ------- */

@media(max-width:1450px){
  .sidebar-left {
    width: 280px;
  }
  .sidebar-column-2 {
    width: calc(100% - 70px);
  }
  .sidebar-left .sidebar-column-1 {
    width: 70px;
  }
  .sidebar-left .user-list .user-image {
    padding: 0 15px;
  }
  .main-wrapper {
    margin-left: 280px;
  }
  .sidebar-left .sidebar-menu ul li a {
    padding: 10px 20px;
  }
  .sidebar-left  .px-30 {
    padding-left: 20px;
    padding-right: 20px;
  }
  .profile-card .user-img-name .img-wrap {
    max-width: 115px;
  }

}
@media(max-width:1199px){ 
  .main-wrapper {
    margin-left: 0;
  }
  #sidebar.show .sidebar-left {
    transform: translateX(0);
    -webkit-transform: translateX(0);
  }
  #sidebar:not(.show) .sidebar-left {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
  }
  .header-section {
    padding-left: 15px;
    padding-right: 15px;
  }
  .header-section h2 {
   margin-top:15px;
  }
}

@media(max-width:575px){
  .header-section {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .header-section h2 {
    margin-top: 0;
  }
  .header-section .user-header .img-wrap {
    max-width: 40px;
  }
}